@mixin vf-select-up-chevron() {
  background-image: url("data:image/svg+xml;base64,");
}

@mixin vf-select-down-chevron {
  background-image: url("data:image/svg+xml;base64,");
}

@mixin vf-select-up-chevron-disabled {
  background-image: url("data:image/svg+xml;base64,");
}

@mixin vf-select-down-chevron-disabled {
  background-image: url("data:image/svg+xml;base64,");
}

.vf-dropdown {

  position: relative;
  width: 300px;

  .vf-dropdown-label {

    display: block;
    background: $white;
    padding: 12px 114px 12px 20px;
    margin-bottom: 0;
    border-radius: 4px 4px 4px 4px;
    border: 1px solid $vattenfall-light-border-grey;
    color: $dark;
    font-size: $font-size-lg;
    line-height: 24px;
    font-weight: normal;
    letter-spacing: 0px;
    cursor: pointer;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;

    &:hover {

      border: 1px solid $vattenfall-blue-grey;

    }

    &:after {

      content: "";
      background-repeat: no-repeat;
      background-size: 36px;
      background-position: right center;
      @include vf-select-down-chevron();
      //padding-left: 120px;
      position: absolute;
      top: 0;
      bottom: 0;
      right: 8px;
      padding: 18px;
    }
  }

  .vf-dropdown-list {

    position: absolute;
    left: 0;
    right: 0;
    background: #fff;
    transform-origin: 50% 0;
    transform: scale(1, 0);
    //transition: transform .15s ease-in-out .15s;
    max-height: 500px;
    overflow-y: scroll;

    .vf-dropdown-list-item {

      padding: 12px 20px;

      &:hover, &.checked {

        background-color: $vattenfall-button-blue;
        color: #fff;

      }

      input {

        appearance: none;
        background: $white;
        border-radius: 4px 4px 4px 4px;
        border: 1px solid $vattenfall-dropdown-item-border;
        height: 28px;
        width: 28px;
        line-height: 1;
        position: relative;
        vertical-align: middle;
        margin-right: 12px;
        cursor: pointer;

        &:before {

          content: "";
          position: absolute;
          transition: transform .2s ease-in-out;
          border-bottom: 3px solid $vattenfall-dropdown-item-border-before;
          border-right: 3px solid $vattenfall-dropdown-item-border-before;
          height: 16px;
          width: 8px;
          margin: auto;
          left: 9px;
          top: 3px;

          transform: rotate(45deg) scale(0);

        }

        &:checked {

          &:before {

            transform: rotate(45deg) scale(1);
            border-bottom: 3px solid $btn-outline-dark-border-focus;
            border-right: 3px solid $btn-outline-dark-border-focus;

          }
        }

        &:hover {

          &:before {

            transform: rotate(45deg) scale(1);

          }
        }

        &:disabled {

          background: $vattenfall-dropdown-input-disabled;

          &:before {

            transform: rotate(45deg) scale(0);

          }

          &.pre-selected {

            &:before {

              transform: rotate(45deg) scale(1);
              border-bottom: 3px solid $vattenfall-dropdown-item-border-before;
              border-right: 3px solid $vattenfall-dropdown-item-border-before;

            }

          }

        }

      }

      .vf-dropdown-option {

        display: inline-block;
        margin-bottom: 0;
        opacity: 0;
        transition: opacity .15s ease-in-out;
        font-size: $font-size-lg;
        font-weight: normal;
        letter-spacing: 0px;
        line-height: 22px;
        vertical-align: middle;
        max-width: 210px;
        word-break: break-all;
        hyphens: auto;

      }
    }
  }

  &.vf-dropdown--underline {

    .vf-dropdown-label {

      background: none;
      border-radius: 0px 0px 0px 0px;
      border: none;
      border-bottom: 1px solid $vattenfall-dropdown-underline-border;

      &:hover {

        border-color: $vattenfall-blue-grey;

      }
    }

    &.disabled {

      .vf-dropdown-label, .vf-dropdown-list {

        color: $btn-disabled-color;
        background: none;
        border-color: $vattenfall-dropdown-underline-border;

      }
    }

    &.on {

      .vf-dropdown-label, .vf-dropdown-list {

        background: #fff;

      }
    }
  }

  &.vf-dropdown--semantic {
      border: 1px solid #E8E8E9;
      height: 50px;
      width: 300px;
      border-radius: 4px;
      appearance: none;
      -webkit-appearance: none;
      padding: 11px 114px 11px 20px;
      color: #222222;
      font-size: 1.25rem;
      line-height: 24px;
      cursor: pointer;
      &:hover {
        border: 1px solid #000;
      }
      &:focus {
        outline: none;
      }
      &.disabled {
        color: $btn-disabled-color;
        background: $vattenfall-dropdown-input-disabled;
        border-color: $vattenfall-dark-border-grey;
        pointer-events: none;
      }
      &:disabled {
        color: $btn-disabled-color;
        background: $vattenfall-dropdown-input-disabled;
        border-color: $vattenfall-dark-border-grey;
      }
  }

  &.on {

    .vf-dropdown-label {

      border: 1px solid $vattenfall-blue-grey;
      border-bottom: none;
      border-radius: 4px 4px 0 0;

      color: $btn-disabled-color;
      //font-size: 18px; //Resizes the dropdown

      &:after {

        content: "";
        background-repeat: no-repeat;
        background-size: 36px;
        background-position: right center;
        @include vf-select-up-chevron();

      }
    }

    .vf-dropdown-list {

      border-radius: 0 0 4px 4px;
      border: 1px solid $vattenfall-blue-grey;
      border-top: none;
      transform: scale(1, 1);
      transition-delay: 0s;

      .vf-dropdown-option {

        opacity: 1;
        transition-delay: .2s;

      }

    }

    .vf-dropdown-label:after {

      @include vf-select-up-chevron();

    }

    &.disabled {

      .vf-dropdown-label {

        &:after {

          @include vf-select-up-chevron-disabled();

        }
      }

      .vf-dropdown-label {

        background: #fff;
        color: $btn-disabled-color;
        font-size: $font-size-md;

      }

      .vf-dropdown-list {

        background: #fff;
        color: $dark;

      }
    }
  }

  &.disabled {

    .vf-dropdown-label {

      &:after {

        @include vf-select-down-chevron-disabled();

      }
    }

    .vf-dropdown-label, .vf-dropdown-list {

      color: $btn-disabled-color;
      background: $vattenfall-dropdown-input-disabled;
      border-color: $vattenfall-dark-border-grey;

      .vf-dropdown-list-item {

        &:hover {

          background-color: inherit;
          color: inherit;

        }
      }
    }
  }
}

.vf-semantic-dropdown-wrapper {
  position: relative;
  &:after {
    content: "";
    background-repeat: no-repeat;
    background-size: 36px;
    background-position: right center;
    @include vf-select-down-chevron();
    position: absolute;
    top: 0;
    bottom: 0;
    right: 8px;
    padding: 18px;
    pointer-events: none;
  }
}
