.vf-file-list {
  padding: 0;
  margin: 0; 
  list-style-type: none;
}
.vf-file-list__item {
  padding: 6px 0;
  line-height: 24px;
}
.vf-file-list__link {
  color: $link-color;
  font-size: 20px;
  font-weight: 500;
  letter-spacing: 0px;
  line-height: 36px;

  &:before {
    content: '';
    display: inline-block;
    width: 19px;
    height: 24px;
    background-color: transparent;
    background-repeat: no-repeat;
    background-position: 0 0;
    background-size: 19px 24px;
    margin-right: 10px;
    vertical-align: middle;
    background-image: url('data:image/svg+xml;base64,')
  }

 &.vf-file-list__link--type-download {
    &:before {
      background-image: url("data:image/svg+xml;base64,")    
    }
  }

  &.vf-file-list__link--type-xls,
  &[href$=".xls"], 
  &[href$=".xlsx"] {
    &:before {
      background-image: url('data:image/svg+xml;base64,');
    }
  }
  &.vf-file-list__link--type-txt,
  &[href$=".txt"]{
    &:before {
      background-image: url('data:image/svg+xml;base64,');
    }
  }

  &.vf-file-list__link--type-ppt,
  &[href$=".ppt"], 
  &[href$=".pptx"]{
    &:before {
      background-image: url('data:image/svg+xml;base64,');
    }
  }

  &.vf-file-list__link--type-pdf,
  &[href$=".pdf"] {
    &:before {
      background-image: url('data:image/svg+xml;base64,');
    }
  }

  &.vf-file-list__link--type-doc,
  &[href$=".doc"], 
  &[href$=".docx"]{
    &:before {
      background-image: url('data:image/svg+xml;base64,');
    }
  }

  &.vf-file-list__link--type-new-window {
    &:before {
      background-image: url('data:image/svg+xml;base64,')
    }
  }

  &.vf-file-list__link--type-arrow-right {
    &:before {
      background-image: url('data:image/svg+xml;base64,')
    }
  }
}

.vf-file-list__item-description {
  color: $vattenfall-metadata-grey;
  font-size: 14px;
  margin-left: 20px;
}
